import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import Form from './styles/Form';
import Error from './ErrorMessage';


class Signup extends Component {
  render() {
    return (
      <Form>
        <fieldset>
          <h2>Sign Up for an Account</h2>
          <label htmlFor="email">
            email
            <input
             type="text" name="email" placeholder="email" value={this.state.email} onChange={this.saveToState}
            />
          </label>
          <label htmlFor="name">
            name
            <input
             type="text" name="name" placeholder="name" value={this.state.name} onChange={this.saveToState}
            />
          </label>
          <label htmlFor="password">
            password
            <input
             type="text" name="password" placeholder="password" value={this.state.password} onChange={this.saveToState}
            />
          </label>
        </fieldset>
      </Form>
    )
  }
}

export default Signup